*, ::after, ::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: #FFC64F;
}

html {
  width: 100%;
  height: 100%;
}

.button {
  text-decoration: none;
  border: 1px solid #ffffff;
  border-radius: 25px;
  font-family: "Play";
  color: #ffffff;
  font-weight: bold;
  text-transform: uppercase;
}

ul {
  list-style-type: none;
}

.intro__button {
  display: flex;
  font-size: 26px;
  align-items: center;
  justify-content: center;
  max-width: 300px;
  height: 50px;
  margin-left: auto;
  margin-right: auto;
  margin-top: 30px;
  margin-bottom: 30px;
}
.intro__button:hover {
  transition: 0.8s;
  color: #FFC64F;
  background-color: #ffffff;
}
.intro__button:active {
  transform: scale(1.2);
}

.intro {
  width: 100%;
  display: flex;
  flex-direction: column;
  background-image: url("../img/Promo/bg.svg");
  background-repeat: no-repeat;
  background-size: cover;
}

.intro__wrapper {
  width: 90%;
  margin: 0 auto;
}

.intro__header {
  display: flex;
  justify-content: flex-end;
  margin-top: 30px;
  margin-bottom: 30px;
}

.intro__burger {
  background-image: url("../img/Promo/burger-button.svg");
  border: none;
  background-color: transparent;
  width: 25px;
  height: 16.5px;
  cursor: pointer;
  position: fixed;
  left: 6px;
  z-index: 10;
}

.intro__social-item {
  display: inline;
  text-decoration: none;
}

.intro__logo {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 80%;
  margin-left: auto;
  margin-right: auto;
}
.intro__logo::after {
  content: " ";
  background-image: url("../img/Promo/date.svg");
  background-position: center;
  width: 100px;
  height: 100px;
  position: relative;
  background-size: contain;
  left: 70%;
}

@media (min-width: 765px) {
  .intro__burger {
    display: none;
  }
  .intro__wrapper {
    display: block;
    max-width: 1258px;
  }
  .intro__header {
    justify-content: flex-end;
    padding-top: 50px;
  }
  .intro__logo {
    width: 50%;
    margin-left: auto;
    margin-right: auto;
  }
  .intro__logo::before {
    content: "";
    background-image: url("../img/Promo/logo-before.svg");
    width: 137px;
    height: 137px;
    background-position: center;
    background-size: contain;
    position: relative;
    top: -29%;
    left: -25%;
  }
  .intro__logo::after {
    width: 140px;
    height: 140px;
    position: relative;
    left: 95%;
    top: 82%;
  }
}
.toggle {
  flex-direction: column;
  background-image: url("../img/toggle-Line.svg");
  background-repeat: repeat-y;
  background-position: center;
  width: 10px;
  display: none;
}

.toggle__item:nth-child(7) {
  margin-bottom: 0;
}

.toggle__item {
  width: 10px;
  height: 10px;
  background-color: #ffffff;
  border-radius: 50%;
  margin-bottom: 30px;
}

@media (min-width: 765px) {
  .toggle {
    display: flex;
    position: fixed;
    right: 17px;
    top: 250px;
    z-index: 15;
